    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    
    body {
        width: 100%;
        min-height: 100vh;
        font-family: "Montserrat", sans-serif;
        background-color: #223343;
    }
    
    @media (max-width: 600px) {
        body {
            width: 50vw;
        }
    }